import { FC } from 'react';
import * as classes from './AAButton.module.scss'
import { Button } from 'reactstrap';
import IconeDoneSVG from '../../../assets/img/product-row/icon-done.svg'

interface AButtonProps {
    title: string
    isActive?: boolean
    color?: 'green' | 'blue' | 'fiolet' | 'orange' | 'light' | 'dark' | 'april'
    | 'warning' | 'danger' | 'success'
    size?: 'small' | 'medium' | 'big'
    clickHendler?: (clickHendlerData: any | null) => void
    clickHendlerData?: { [key: string]: any }
    isIconeDone?: boolean


}


export const AButton: FC<AButtonProps> = ({
    title,
    isActive=true,
    color = "april",
    size = "medium",
    clickHendler,
    clickHendlerData,
    isIconeDone

}) => {
    const buttonClass = `${classes.button} ${classes[`button--${color}`]} 
    ${isActive ? classes.active : classes.inactive}
       ${isIconeDone ? classes.withIcone : ''} 
       ${classes[size]}
    
    `;
    debugger
    return (
        <Button
            //@ts-ignore
            // color={false}
            className={`${buttonClass} `} size='sm'

            onClick={
                () => clickHendler && clickHendler(clickHendlerData || null)

            }
        >
            {title}
            {isIconeDone && <IconeDoneSVG
                width={'30px'}
                height={'30px'}
            // color={'color'} 
            />}
        </Button>
    );
}

